<template>
	<view class="content">
		<view class="newfile-days">
			<scroll-view scroll-x>
				<view class="newfile-scroll">
					<view class="newfile-day" :style="{'color':tabIndex==index?theme:'#333',
					'border-color':tabIndex==index?theme:'#ddd'}"
					 v-for="(day,index) in daylist" :key="index" @tap="toggleIndex(day,index)">
						<view class="newfile-week">{{day.week}}</view>
						<view class="newfile-date">{{day.month}}/{{day.day}}</view>
					</view>
				</view>
			</scroll-view>
		</view>
		<view class="news_files_content">
			<view class="news_files_timer" :style="{'color':itemIndex==index?theme:'#333'
			,'border-color':itemIndex==index?theme:'#ddd'}"
			 :class="{'w-time-item-active':!item.disabled}" v-for="(item,index) in list" :key="index" @tap="toggleItem(item,index)">
				<text>{{item.timer}}--{{item.timers}}</text>
			</view>
		</view>
	</view>
	</view>
</template>

<script>
	import { dateData, timeData, timeStamp } from './date.js';
	export default {
		props: {
			afterDays: {
				type: [String, Number],
				default: 7
			},
			theme: {
				type: String,
				default: "#f5a200"
			},
			step: {
				type: [String, Number],
				default: 60
			},
			afterHours: {
				type: [String, Number],
				default: 0
			}
		},
		data() {
			return {
				daylist: [],
				tabIndex: 0,
				itemIndex: -1,
				timer1: 8,
				timer2: 14,
				timer3: 19,
				time: "",
				list: [{
						timer: "8:00",
						timers: "12:00",
					},
					{
						timer: "14: 00",
						timers: "18:00",
					},
					{
						timer: "19: 00",
						timers: "22:00",
					}
				]
			}
		},
		mounted() {
			this.initPicker();
			this.initHours();
		},
		methods: {
			toggleIndex(item, index) {
				this.tabIndex = index;
				this.itemIndex = -1;
				this.initHours(item.isday)
			},
			toggleItem(item, index) {
				if (!item.disabled) {
					this.itemIndex = index;
					this.time = item;
				}
			},
			forMatNumber(n) {
				return n < 10 ? '0' + n : n
			},
			initHours(flag) {
				let aDate = new Date();
				let curHour = aDate.getHours();
				console.log(curHour)
				console.log(this.timer1)

				if (flag) {
					disabled: false
				}
				else {
					disabled: curHour + this.afterHours > this.timer1||this.timer2||this.timer3? true : false
				}

			},
			initPicker() {
				let aDate = new Date();
				let weekList = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
				this.daylist.push({
					year: aDate.getFullYear(),
					month: this.forMatNumber(aDate.getMonth() + 1),
					day: this.forMatNumber(aDate.getDate()),
					week: weekList[aDate.getDay()],
					isday: false,
				})
				for (let i = 1; i < this.afterDays * 1; i++) {
					aDate.setDate(aDate.getDate() + 1);
					this.daylist.push({
						year: aDate.getFullYear(),
						month: this.forMatNumber(aDate.getMonth() + 1),
						day: this.forMatNumber(aDate.getDate()),
						week: weekList[aDate.getDay()],
						isday: true,
					})
				};
			},


		},
		components: {

		}
	}
</script>

<style scoped>
	.newfile-days {
		overflow: hidden;
		padding: 20upx 0;
		background-color: #fff;
	}

	.newfile-scroll {
		white-space: nowrap;
	}

	.newfile-day {
		display: inline-block;
		width: 95upx;
		text-align: center;
		border: solid 1px #ddd;
		margin: 0 5upx;
		border-radius: 6upx;
		padding: 10upx 0;
		color: #333;
	}

	.newfile-week {
		font-size: 28upx;
		line-height: 1;
	}

	.newfile-date {
		font-size: 24upx;
		line-height: 1;
		margin-top: 10upx;
	}

	.newfile-day-active {
		color: #f00;
		border-color: #f00;
	}

	.news_files_content {
		height: 100rpx;
		width: 100%;

	}

	.news_files_timer {
		float: left;
		margin-left: 30rpx;
		text-align: center;
		line-height: 50rpx;
		height: 50rpx;
		width: 200rpx;
		font-size: 30rpx;
		border: 1px solid #000000;
	}
</style>
